@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: "Square Sans";
    src: url(/Demo-countdown-clock/static/media/square_sans_serif_7.1027aa7c.ttf);
}
@font-face {
    font-family: "Genica";
    src: url(/Demo-countdown-clock/static/media/SVN-Genica_Pro.afd18e45.otf);
}
.clock-container {
    position: relative;
}

.clock-counter {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color: #7894FF; /* light purple */
    font-family: "Genica", sans-serif;
    font-weight: 600;
    margin: 0 2rem; 
    transition: opacity 2s ease-in;
}
.clock-timer {
    font-size: 3.4rem;
    top: 0;
    left: 0;
    width: 100%;
    height: 70%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    object-fit: cover;
    position: absolute;
    transition: opacity 2s ease-in;
}
.clock-unit {
    font-size: 1.8rem;
    position: relative;
    transition: opacity 2s ease-in;
}

.progress-bar {
    position: relative;
}
.progress-bar svg {
    position: relative;
    width: 150px;
    height: 150px;
}
.progress-bar svg circle { 
    width: 100%;
    height: 100%;
    fill: none;
    stroke: #7894FF; /* light purple */
    stroke-width: 10;
    -webkit-transform: translate(5px, 5px);
            transform: translate(5px, 5px);
    transition-duration: 1s;
}
.progress-bar svg circle:nth-child(2) {
    stroke: #FF9D72; /* light orange */
    stroke-dasharray: 440; 
}
@font-face {
    font-family: "Genica";
    src: url(/Demo-countdown-clock/static/media/SVN-Genica_Pro.afd18e45.otf);
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    background: #E6EBFF; /* light purple */
    background-repeat: no-repeat;
    background-size: cover;
}
h1, h2, h3, h4, h5, h6 {
    font-family: "Genica", sans-serif;
    margin-bottom: 1rem;
    letter-spacing: 0.1rem;
}
.countdown-timer {
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}
.countdown-timer h2{ 
    color: #7894FF; /* light purple */
    font-size: 3rem;
}
.countdown-timer h4{ 
    color: #7894FF; /* light purple */
    font-size: 1.3rem;
}
.clock-container {
    max-width: 60%;
    display: -webkit-flex;
    display: flex; 
    -webkit-flex-direction: row; 
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    padding: 2rem 0;
}

.loading-screen{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100vw;
    height: 100vh;
}
.loading-screen h2 {
    color: #7894FF;
    font-size: 2rem;
}
.dialog-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
}
.login-button {
    font-family: "Genica", sans-serif;
    font-size: 1.1rem;
    font-weight: 100;
    letter-spacing: 0.1rem;
    width: 140px;
    height: 60px;
    border: none;
    border-radius: 12px;
    background-color: #8099fd;
    color: #ff9e75;
    transition-duration: 0.4s;
}
.login-button:hover,
.login-button:active {
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}
.dialog-container {
    background-image: url(/Demo-countdown-clock/static/media/dialogBackground.d2bee5e0.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 80%;
    height: 80%;
    border-radius: 15px;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);;
}
.submit-button {
    background-color: transparent;
    border: 1px solid white;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    font-size: 1rem;
    color: white;
    width: 6rem;
    height: 2.5rem;
    transition-duration: 0.4s;
}
.submit-button:hover {
    background-color: rgba(255,255,255, 0.4);
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}
.input-form {
    font-family: 'Noto Sans', sans-serif;
}
.input-form input:not(#checkbox) {   
    border: 1px solid rgba(255, 255, 255, 0.836);
    color: white;
    border-radius: 5px;
    background-color: transparent;
    width: 100%;
    height: 2.5rem;
    padding: 0.5rem;
    margin-bottom: 1rem;
}
#checkbox {
    margin-bottom: 2rem;
}
.input-form label{
    font-family: 'Open Sans', sans-serif;
    color: white;
    padding: 0 0.5rem;
}
.input-form label a {
    text-decoration: none;
    color: cyan;
}
.input-form label a:hover {
    text-decoration: underline;
}
.input-form input::-webkit-input-placeholder{
    color: rgba(255, 255, 255, 0.781);
}
.input-form input:-ms-input-placeholder{
    color: rgba(255, 255, 255, 0.781);
}
.input-form input::placeholder{
    color: rgba(255, 255, 255, 0.781);
}
.alert-validate {
    color: white;
    line-height: 10px;
}
.signup-container{
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    right: 0;
    background-color: rgba(33, 89, 130, 0.705); /* opacity */
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
    border-radius: 15px;
    padding: 1rem;
    width: 25%;
    height: 100%;
}
.content-container {
    display: block;
}
.content-container p{
    color: rgb(170, 168, 168);
    font-family: sans-serif;
    font-size: 0.8rem;
    font-weight: 100;
    margin-bottom: 1rem;
}
/* description */
.description {
    font-family: 'Open Sans', sans-serif;
    color: white;
}
.description h2{
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
}
/* back button */
.back-button {
    position: absolute;
    top: 10px;
    left: 10px;
    font-family: 'Genica', sans-serif;
    font-size: 1.2rem;
    color: rgb(41, 50, 53);
    margin: 0.8rem 0;
    border: none;
    background: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 2px;
    transition-duration: 0.4s;
}
.back-button:hover {
    -webkit-transform: translate(-8px);
            transform: translate(-8px);
}
